<template>
  <div id="app">
    <div class="container">
      <div class="row">
        <h1 v-text="title"></h1>
      </div>
      <div class="row">
          <div class="input-group">
            <input type="text" class="form-control" placeholder="add event" v-model="newItem" v-on:keyup.enter="addNew()">
            <span class="input-group-btn">
              <button class="btn btn-success" type="button" v-on:click="addNew()">Go!</button>
            </span>
          </div>
      </div>
      <div class="row">
        <ul class="list-unstyled">
          <li class="todolist" v-for="item in items" v-bind:class="{finished: item.isFinished}" v-on:click="toggleFinish(item)">
            <!--itemsitems-->
            {{item.label}}
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import Store from './store'
  console.log(Store)
  export default {
    data: function () {
      return {
        title: 'I want to do',
        items: Store.fetch(),
        newItem: ''
      }
    },
    watch: {
      items: {
        handler: function (items) {
          Store.save(items)
        },
        deep: true
      }
    },
    methods: {
      toggleFinish: function (item) {
        item.isFinished = !item.isFinished
      },
      addNew: function () {
        this.items.push({
          label: this.newItem,
          isFinished: false
        })
        this.newItem = ''
      }
    }
  }
</script>

<style>

.finished {
  text-decoration: line-through;
  color: #ccc;
}

li.todolist {
  margin: 15px 0 15px 10px;
}

</style>



